import cartControl from '../../components/cartControl/cartControl.vue';
// import {cartControlMixin} from '../../common/mixins.js';

export default {
	data() {
		return {
			mainArray: [],
			topArr: [],
			leftIndex: 0,
			isMainScroll: false,
			scrollInto: '',
			mainScrollInit: 0,
			tipsTop: '0px'
		};
	},
	props: {
		goodList: {
			type: Array,
			default: () => []
		}
	},
	// mixins: [cartControlMixin],
	created() {
	},
	computed: {},
	mounted() {
		let that = this;
		that.$nextTick(() => {
			uni
				.createSelectorQuery()
				.in(this)
				.select('#item-0')
				.boundingClientRect(data => {
					that.mainScrollInit = data.top;
				})
				.exec();

			that.goodList.forEach(function(item) {
				that.mainArray.push(item.foods);
			});

			that.getElementTop();
		});
	},
	components: {
		cartControl
	},
	methods: {
		add(id) {
		  this.$emit('add', id);
		},
		sub(id) {
		  this.$emit('sub', id);
		},
		handleFood(food) {
			this.$emit('handleFood', food)
		},
		//获取距离顶部的高度
		getScrollTop(selector) {
			let that = this;
			return new Promise((resolve, reject) => {
				let query = uni.createSelectorQuery().in(this);
				query
					.select(selector + '')
					.boundingClientRect(data => {
						resolve(data.top - that.mainScrollInit);
					})
					.exec();
			});
		},
		/* 获取元素顶部信息 */
		async getElementTop() {
			// console.log(this.mainArray);
			/* Promise 对象数组 */
			let p_arr = [];

			/* 遍历数据，创建相应的 Promise 数组数据 */
			for (let i = 0; i < this.mainArray.length; i++) {
				const resu = await this.getScrollTop(`#item-${i}`);
				p_arr.push(resu);
			}

			// console.log('p_arr', p_arr)

			/* 主区域滚动容器的顶部距离 */
			this.getScrollTop('#scroll-el').then(res => {
				/* 所有节点信息返回后调用该方法 */
				Promise.all(p_arr).then(data => {
					// console.log('滚动', data);
					this.tipsTop = `${data}px`;
					this.topArr = data;
				});
			});
		},

		/* 主区域滚动监听 */
		mainScroll(e) {
			if (!this.isMainScroll) {
				return;
			}
			let top = e.detail.scrollTop;
			let index = -1;

			if (top >= this.topArr[this.topArr.length - 1]) {
				index = this.topArr.length - 1;
			} else {
				index = this.topArr.findIndex((item, index) => {
					return this.topArr[index + 1] >= top;
				});
			}
			this.leftIndex = index < 0 ? 0 : index;
			// console.log(top);
			// console.log(this.leftIndex);
		},
		/* 主区域触摸 */
		mainTouch() {
			this.isMainScroll = true;
		},
		/* 左侧导航点击 */
		leftTap(e) {
			let index = e.currentTarget.dataset.index;
			this.isMainScroll = false;
			this.leftIndex = Number(index);
			this.scrollInto = `item-${index}`;
		},
		touchStart(e) {
			// console.log(e);
		}
	}
};
